* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button[data-start], button[data-stop] {
  color: #fff;
  cursor: pointer;
  background-color: green;
  border-color: #fff;
  border-radius: 15px;
  outline: none;
  padding: 10px;
  font-size: 16px;
}

button:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}

#datetime-picker {
  text-align: center;
  color: #212121;
  border: 1px solid;
  border-radius: 5px;
  outline: none;
  margin-bottom: 15px;
  padding: 5px;
  font-size: 20px;
}

.btntimer {
  width: 100px;
  outline: none;
  font-weight: 700;
}

.timer {
  gap: 20px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  display: flex;
}

span {
  font-size: 20px;
}

.value {
  font-weight: bold;
}

